import { IAuthToken } from "../interfaces/IAuthToken";

export const fetchAuth = async (code: string) => {
  try {
    // let dupa = window.location.href;
    let dupa = "https://lziobro.github.io/completionistv2";
    if (dupa.lastIndexOf("/") === dupa.length) {
      dupa = dupa.slice(0, -1);
    }
    let resp = await fetch(
      `${process.env.REACT_APP_BASE_API_URL}/fetchToken?code=${code}&returnUrl=${dupa}`,
      {
        method: "GET",
      }
    );
    if (resp.ok) {
      resp = await resp.json();
      return resp as unknown as IAuthToken;
    }
  } catch (err) {
    console.log("CANT FETCH AUTH TOKEN FROM OSU WEB");
  }
};

export const fetchSearchUserNode = async (
  authTokenString: string,
  username: string
) => {
  try {
    let resp = await fetch(
      `${process.env.REACT_APP_BASE_API_URL}/fetchSearchUser?authTokenString=${authTokenString}&username=${username}`,
      {
        method: "GET",
      }
    );
    if (resp.ok) {
      resp = await resp.json();
      return resp;
    }
  } catch (err) {
    console.log(err);
  }
};
